S Libraries\n====================================== */\n\n@import url(\"css/normalize.css\");\n@import url(\"css/clear.css\");\n@import url(\"css/sm-clean.css\");\n/* @import url(\"css/owl.carousel.min.css\"); */\n@import url(\"css/owl.theme.default.min.css\");\n@import url(\"css/prettyPhoto.css\");\n@import url(\"css/common.css\");\n\n\n/* ===================================\n    2. Global CSS\n====================================== */\n\n*,\n::after,\n::before {\n    box-sizing: border-box;\n}\n\nbody {\n    font-family: 'Poppins', sans-serif;\n    font-size: 15px;\n    line-height: 30px;\n    font-weight: 400;\n    color: #9B8347;\n    background-color: #fff;\n    overflow-x: hidden;\n}\n\nbody a {\n    text-decoration: none;\n    color: #000000;\n    transition: color .3s linear;\n}\n\nbody a:hover {\n    color: #acacac;\n}\n\nbody img {\n    max-width: 100%;\n    max-height: 100%;\n    width: auto;\n    height: auto;\n    display: block;\n}\n\n.doc-loader {\n    position: fixed;\n    left: 0px;\n    top: 0px;\n    width: 100%;\n    height: 100%;\n    z-index: 99999;\n    background-color: #0e1111;\n}\n\n.doc-loader img {\n    width: 50px;\n    height: 50px;\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n}\n\np {\n    margin-bottom: 30px;\n}\n\n.content-1300 {\n    max-width: 1300px;\n}\n\n.section-info {\n    font-size: 22px;\n    line-height: 35px;\n}\n\na.button {\n    display: inline-block;\n    color: #9B8347;\n    border: 2px solid #d4af37;\n    text-align: center;\n    padding: 12px 38px;\n    cursor: pointer;\n    vertical-align: middle;\n    text-decoration: none;\n    border-radius: 50px;\n    transition: all .2s linear;\n}\n\na.button:active {\n    position: relative;\n    top: 1px;\n}\n\na.button:hover {\n    color: white;\n    background-color: #d4af37;\n}\n\n\n/* ===================================\n    3. Left Part Content (Sidebar) CSS\n====================================== */\n\n.content-left {\n    position: fixed;\n    width: 380px;\n    background-color: #0e1111;\n    height: 100vh;\n    z-index: 1;\n}\n\n.content-left::-webkit-scrollbar-track {\n    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);\n    background-color: #554247;\n}\n\n.content-left::-webkit-scrollbar {\n    width: 7px;\n    background-color: #554247;\n}\n\n.content-left::-webkit-scrollbar-thumb {\n    background-color: #d4af37;\n}\n\n.content-left-wrapper {\n    padding-left: 75px;\n    position: relative;\n}\n\n.toggle-holder {\n    position: fixed;\n    top: 30px;\n    left: 30px;\n    z-index: 3;\n}\n\n#toggle {\n    height: 23px;\n    width: 30px;\n    position: relative;\n}\n\n#toggle:hover {\n    cursor: pointer;\n}\n\n#toggle:before,\n#toggle:after {\n    content: '';\n    height: 3px;\n    width: 30px;\n    position: absolute;\n    -webkit-transform-origin: 50% 50%;\n    transform-origin: 50% 50%;\n    -webkit-transition: -webkit-transform 0.25s;\n    -webkit-transition: all 0.25s;\n    transition: all 0.25s;\n    top: 8px;\n    background-color: #fff;\n}\n\n#toggle:before {\n    -webkit-transform: translate3d(0, -8px, 0);\n    transform: translate3d(0, -8px, 0px);\n    width: 21px;\n    margin-left: 0;\n}\n\n#toggle:hover:before {\n    margin-left: 9px;\n\n}\n\n#toggle.on:before {\n    -webkit-transform: rotate3d(0, 0, 1, 45deg) translate3d(0, 0, 0);\n    transform: rotate3d(0, 0, 1, 45deg) translate3d(0, 0, 0);\n    background-color: #fff;\n    margin-left: 0;\n    width: 26px;\n}\n\n#toggle.on:after {\n    -webkit-transform: rotate3d(0, 0, 1, -45deg);\n    transform: rotate3d(0, 0, 1, -45deg);\n    background-color: #fff;\n    width: 26px;\n}\n\n#toggle:after {\n    -webkit-transform: translate3d(0, 8px, 0);\n    transform: translate3d(0, 8px, 0);\n}\n\n#toggle .menu-line {\n    width: 13px;\n    height: 3px;\n    position: absolute;\n    -webkit-transition: all 0.25s;\n    transition: all 0.25s;\n    margin-left: 0;\n    top: 8px;\n    background-color: #fff;\n}\n\n#toggle:hover .menu-line {\n    margin-left: 17px;\n}\n\n#toggle.on .menu-line {\n    opacity: 0;\n}\n\n.top-pagination {\n    position: relative;\n    z-index: 2;\n    padding-top: 150px;\n}\n\n.top-pagination > div {\n    display: inline-block;\n    font-weight: 700;\n    vertical-align: top;\n}\n\n.current-num {\n    width: 25px;\n    position: relative;\n    height: 30px;\n    color: #d4af37;\n}\n\n.current-num span {\n    position: absolute;\n    left: 0;\n}\n\n.total-pages-num {\n    color: #fff;\n}\n\n.pagination-div {\n    position: relative;\n    width: 100px;\n    height: 30px;\n    margin-right: 8px;\n}\n\n.pagination-div:after {\n    content: \"\";\n    display: block;\n    position: absolute;\n    height: 3px;\n    width: 100%;\n    background-color: #fff;\n    top: 12px;\n}\n\n.my-info-wrapper {\n    padding-top: 110px;\n}\n\n.my-info {\n    margin-bottom: 30px;\n}\n\n.my-info > p {\n    margin-bottom: 0;\n}\n\n.my-info-title {\n    font-size: 12px;\n    line-height: 100%;\n    color: #d4af37;\n    letter-spacing: 5px;\n}\n\n.my-info-content {\n    color: #9B8347;\n}\n\nimg.my-info-signature {\n    width: 215px;\n    padding-top: 35px;\n    padding-bottom: 30px;\n}\n\n.big-num {\n    position: fixed;\n    width: 380px;\n    left: 0;\n    bottom: 30px;\n    padding-left: 75px;\n    z-index: 1;\n}\n\n.current-big-num {\n    font-size: 160px;\n    -webkit-text-stroke: 3px #d4af37;\n    color: transparent;\n    font-weight: 800;\n    line-height: 100%;\n    letter-spacing: -4px;\n    position: relative;\n    margin-top: 20px;\n    z-index: 2;\n}\n\n.icon-scroll,\n.icon-scroll:before {\n    position: absolute;\n    right: -18px;\n}\n\n.icon-scroll {\n    width: 38px;\n    height: 60px;\n    margin-left: -20px;\n    bottom: 25px;\n    margin-top: -35px;\n    box-shadow: inset 0 0 0 3px #d4af37;\n    border-radius: 25px;\n    transition: right .5s;\n}\n\n.icon-scroll:before {\n    content: '';\n    width: 8px;\n    height: 8px;\n    background: #d4af37;\n    margin-left: -4px;\n    top: 8px;\n    border-radius: 4px;\n    -webkit-animation-duration: 1.5s;\n    animation-duration: 1.5s;\n    -webkit-animation-iteration-count: infinite;\n    animation-iteration-count: infinite;\n    -webkit-animation-name: scroll;\n    animation-name: scroll;\n    left: 50%;\n}\n\n.icon-scroll:after {\n    content: \"\";\n    position: absolute;\n    z-index: -1;\n    background-color: #0e1111;\n    width: 58px;\n    height: 80px;\n    border-radius: 25px;\n    transform: translate(-10px, -10px);\n    transition: all .3s;\n}\n\n@-webkit-keyframes scroll {\n    0% {\n        opacity: 1;\n    }\n    100% {\n        opacity: 0;\n        -webkit-transform: translateY(35px);\n        transform: translateY(35px);\n    }\n}\n\n@keyframes scroll {\n    0% {\n        opacity: 1;\n    }\n    100% {\n        opacity: 0;\n        -webkit-transform: translateY(35px);\n        transform: translateY(35px);\n    }\n}\n\n.open .icon-scroll {\n    right: 30px;\n    z-index: 1;\n}\n\n.open .icon-scroll:after {\n    background-color: transparent;\n}\n\n\n/* ===================================\n    3.1. Menu CSS\n====================================== */\n\n.menu-wrapper {\n    padding-top: 150px;\n}\n\n.menu-holder {\n    padding: 120px 50px 0 75px;\n    background-color: #9B8347;\n    width: 380px;\n    height: 100%;\n    min-height: 100vh;\n    top: 0;\n    left: 0;\n    position: absolute;\n    transform: translateX(-380px);\n    transition: transform .3s linear;\n    z-index: 1;\n}\n\n.open .menu-holder {\n    transform: translateX(0);\n}\n\n.main-menu.sm-clean {\n    background-color: transparent;\n    display: inline-block;\n}\n\n.sm-clean a {\n    position: relative;\n}\n\n.sm-clean a,\n.sm-clean a:hover,\n.sm-clean a:focus,\n.sm-clean a:active {\n    color: #ffffff;\n    padding: 5px 0;\n    font-size: 28px;\n    line-height: 50px;\n    font-weight: 700;\n}\n\n.sm-clean a:after {\n    content: '';\n    display: block;\n    width: 0;\n    height: 16px;\n    background-color: #d4af37;\n    transition: width .3s;\n    position: absolute;\n    left: -5%;\n    bottom: 7px;\n    z-index: -1;\n}\n\n.main-menu.sm-clean .sub-menu li a:after {\n    height: 3px;\n    bottom: 20px;\n}\n\n.sm-clean a.current:after,\n.sm-clean a:hover:after,\n.sm-clean a:active:after,\n.sm-clean a.highlighted:after,\n.sm-clean ul a:hover:after,\n.sm-clean ul a:active:after,\n.sm-clean ul a.highlighted:after,\n.sm-clean li.current > a:after {\n    width: 110%;\n}\n\n.sm-clean a span.sub-arrow {\n    color: #d4af37;\n}\n\n.main-menu.sm-clean .sub-menu li a {\n    color: #848484;\n    text-transform: uppercase;\n    font-size: 14px;\n}\n\n\n/* ===================================\n    4. Right Part Content CSS\n====================================== */\n\n.content-right {\n    margin-left: 380px;\n    width: calc(100% - 380px);\n}\n\n.content-right-wrapper > div:last-of-type {\n    padding-bottom: 150px;\n}\n\n.section {\n    background-color: #0e1111;\n    position: relative;\n    padding-top: 150px;\n}\n\n.section-wrapper {\n    background-color: #0e1111;\n    padding-left: 5%;\n    padding-right: 5%;\n}\n\n.full-width-section.section {\n    padding: 0;\n}\n\n.full-width-section .section-wrapper {\n    padding: 0 0 0 5%;\n    height: 100vh;\n    margin: 0;\n    display: flex;\n    display: -webkit-flex;\n    align-items: center;\n    background-color: #fff;\n}\n\nh2.section-title {\n    font-size: 30px;\n    line-height: 100%;\n    margin-bottom: 30px;\n    padding-top: 0;\n}\n\n\n/* ===================================\n    5. Home Section CSS\n====================================== */\n\n.home-left-part {\n    flex: 0 0 370px;\n    margin-right: 5%;\n}\n\n.site-des {\n    font-size: 28px;\n    color: #492d31;\n}\n\nh1.entry-title {\n    font-size: 54px;\n    line-height: 68px;\n    font-weight: 800;\n    color: #0e1111;\n}\n\n.site-info {\n    margin-top: 25px;\n}\n\n.social-links {\n    margin-top: 10vh;\n    margin-bottom: -5px;\n}\n\n.social-links a {\n    font-size: 12px;\n    color: #0e1111;\n    letter-spacing: 5px;\n    margin-right: 30px;\n}\n\n.social-links a:last-of-type {\n    margin-right: 0;\n}\n\n\n/* ===================================\n    6. Service Section CSS\n====================================== */\n\n#service {\n  height: 100vh;\n}\n\n.services-wrapper {\n    display: flex;\n    display: -webkit-flex;\n    flex-wrap: wrap;\n    -webkit-flex-wrap: wrap;\n}\n\n.service-holder {\n    padding: 36px;\n    width: 47%;\n    margin-right: 6%;\n    margin-bottom: 6%;\n}\n\n.service-holder img {\n    width: 80px;\n    margin-bottom: 30px;\n}\n\n.service-title {\n    font-size: 28px;\n    line-height: 42px;\n    color: #d4af37;\n    font-weight: 700;\n}\n\n.button-group-wrapper {\n    margin-top: 50px;\n}\n\n.button-group-wrapper > a {\n    margin-right: 1vw;\n}\n\n.button-group-wrapper > a:last-of-type {\n    margin-right: 0;\n}\n\n\n\n/* ===================================\n    7. Portfolio Section CSS\n====================================== */\n\n.category-filter {\n    position: absolute;\n    right: 0;\n    top: -20px;\n    z-index: 1;\n    width: 25px;\n    height: 15px;\n    cursor: pointer;\n    transition: all .5s ease;\n}\n\n.category-filter.hide {\n    opacity: 0;\n    transform: translateY(70px);\n}\n\n.category-filter-list > div {\n    margin-bottom: 5px;\n}\n\n.category-filter-list > div:last-of-type {\n    margin-bottom: 0;\n}\n\n.category-filter-icon {\n    width: 20px;\n    height: 3px;\n    background-color: #d4af37;\n    float: right;\n    transition: width .3s ease;\n}\n\n.category-filter:hover .category-filter-icon,\n.category-filter.filter-open .category-filter-icon {\n    width: 24px;\n}\n\n.category-filter-icon:after {\n    content: \"\";\n    display: block;\n    width: 10px;\n    height: 3px;\n    background-color: #d4af37;\n    transform: translate(-5px,8px);\n    transition: transform .3s ease;\n}\n\n.category-filter:hover .category-filter-icon:after,\n.category-filter.filter-open .category-filter-icon:after {\n    transform: translate(4px,8px);\n}\n\n.category-filter-icon:before {\n    content: \"\";\n    display: block;\n    width: 10px;\n    height: 3px;\n    background-color: #d4af37;\n    transform: translate(10px,11px);\n    transition: transform .3s ease;\n}\n\n.category-filter:hover .category-filter-icon:before,\n.category-filter.filter-open .category-filter-icon:before {\n    transform: translate(14px,11px);\n}\n\n.category-filter-list {\n    background-color: #0e1111;\n    padding: 24px 20px;\n    width: 110px;\n    display: none;\n    position: absolute;\n    right: 0;\n    top: 15px;\n    font-size: 13px;\n    line-height: 22px;\n    color: #9B8347;\n    z-index: 1;\n    cursor: pointer;\n    transition: opacity .5s, transform .5s;\n}\n\n.category-filter-list.hide {\n    transform: translateY(70px);\n    opacity: 0;\n}\n\n.category-filter-list .button.is-checked {\n    color: #d4af37;\n}\n\n.isotope.no-transition,\n.isotope.no-transition .isotope-item,\n.isotope .isotope-item.no-transition {\n    -webkit-transition-duration: 0s;\n    -moz-transition-duration: 0s;\n    -ms-transition-duration: 0s;\n    -o-transition-duration: 0s;\n    transition-duration: 0s;\n}\n\n.grid {\n    width: calc(100% + 60px);\n    margin-left: -30px;\n    position: relative;\n    display: block;\n    overflow: hidden;\n}\n\n.grid-item {\n    float: left;\n    font-size: 0;\n    line-height: 0;\n    box-sizing:border-box;\n    -moz-box-sizing:border-box;\n    -webkit-box-sizing:border-box;\n    overflow: hidden;\n    border: 30px solid transparent;\n    width: 50%;\n    transition: opacity .3s;\n}\n\n.grid-item img {\n    display: block;\n    width: 100%;\n    height: auto;\n    max-height: none;\n    max-width: none;\n}\n\n.grid-sizer,\n.grid-item {\n    width: calc(100% / 3);\n}\n\n.grid-item.p-one {\n    width: 100%;\n}\n\n.grid-item.p-one-third {\n    width: calc(100% / 3);\n}\n\n.grid-item.p-two-third {\n    width: calc(200% / 3);\n}\n\n.portfolio-text-holder {\n    position: absolute;\n    width: 100%;\n    height: 100%;\n    left: 0;\n    top: 0;\n    z-index: 1;\n    font-size: 20px;\n    background-color: #d4af37;\n    padding: 20px;\n    transform: translateX(-100%);\n    transition: transform .3s ease;\n}\n\n.grid-item a.item-link:hover .portfolio-text-holder,\n.grid-item.portfolio-content-loading a.item-link .portfolio-text-holder {\n    transform: translateX(0);\n}\n\n#portfolio-grid.portfoio-items-mask .grid-item {\n    opacity: 0.2;\n}\n\n.grid-item.portfolio-content-loading {\n    opacity: 1 !important;\n}\n\n.portfolio-text-wrapper {\n    position: absolute;\n    left: 30px;\n    bottom: 0;\n}\n\n.portfolio-text {\n    font-size: 28px;\n    line-height: 100%;\n    font-weight: 700;\n    color: #9B8347;\n    margin-bottom: 0;\n    padding-bottom: 5px;\n}\n\n.portfolio-cat {\n    font-size: 15px;\n    color: #9B8347;\n    line-height: 100%;\n}\n\n.portfolio-cat:before {\n    content: \"\";\n    display: inline-block;\n    width: 25px;\n    height: 1px;\n    background-color: #9B8347;\n    vertical-align: 4px;\n    margin-right: 5px;\n}\n\n.portfolio-load-content-holder {\n    opacity: 0;\n    transform: translateY(70px);\n    transition: all .5s ease;\n}\n\n.portfolio-load-content-holder.show {\n    transform: translateY(0);\n    opacity: 1;\n}\n\n.portfolio-load-content-holder.show.viceversa {\n    opacity: 0;\n    transform: translateY(70px);\n}\n\n.portfolio-content-wrapper {\n    display: none;\n}\n\n.portfolio-content-wrapper.show {\n    display: block;\n}\n\n.close-icon {\n    background-image: url(public/images/close-left-arrow.png);\n    background-repeat: no-repeat;\n    width: 70px;\n    height: 70px;\n    display: inline-block;\n    background-color: rgb(243, 123, 131);\n    border-radius: 5px;\n    margin-bottom: 20px;\n    background-size: 70px;\n    transition: all 0.3s ease;\n    cursor: pointer;\n}\n\n.close-icon:hover {\n    background-position: -5px 0;\n}\n\n#portfolio-grid {\n    transition: all .5s ease;\n    opacity: 1;\n    transform: translateY(0px);\n}\n\n#portfolio-grid.hide {\n    opacity: 0;\n    transform: translateY(70px);\n}\n\n.grid-item a.item-link.portfolio-content-loading:after,\n.ajax-portfolio.portfolio-content-loading .portfolio-text-holder {\n    opacity: 1;\n}\n\n\n/* ===================================\n    7.1 Pretty Photo CSS\n====================================== */\n\ndiv.pp_default .pp_loaderIcon {\n    display: none !important;\n}\n\ndiv.pp_default a.pp_arrow_previous {\n    background-image: url(public/images/nav_left.png);\n    background-size: 20px 20px;\n    width: 20px;\n    height: 20px;\n    margin-top: 5px;\n    font-size: 0;\n    line-height: 0;\n    text-indent: -99999999px;\n    transition: transform .3s ease;\n}\n\ndiv.pp_default a.pp_arrow_next {\n    background-image: url(public/images/nav_right.png);\n    background-size: 20px 20px;\n    width: 20px;\n    height: 20px;\n    margin-top: 5px;\n    margin-left: 10px;\n    font-size: 0;\n    line-height: 0;\n    text-indent: -99999999px;\n    transition: transform .3s ease;\n}\n\ndiv.pp_default a.pp_arrow_previous:hover {\n    transform: translateX(-5px);\n}\n\ndiv.pp_default a.pp_arrow_next:hover {\n    transform: translateX(5px);\n}\n\ndiv.pp_default .pp_close {\n    background-image: url(public/images/close.png);\n    background-size: 30px 30px;\n    margin-top: 5px;\n    text-indent: -99999999px;\n    font-size: 0;\n    line-height: 0;\n}\n\ndiv.pp_default .pp_next:hover {\n    background: url(public/images/nav_right.png);\n    cursor: pointer;\n    background-repeat: no-repeat;\n    background-position: 95% 50%;\n    background-size: 20px;\n}\n\ndiv.pp_default .pp_previous:hover {\n    background: url(public/images/nav_left.png);\n    cursor: pointer;\n    background-repeat: no-repeat;\n    background-position: 5% 50%;\n    background-size: 20px;\n}\n\ndiv.pp_default .pp_nav .currentTextHolder {\n    padding: 0 0 0 20px;\n    color: #fff;\n